@import url('https://fonts.googleapis.com/css?family=Audiowide');

.tool-link-div {
    display: inline-block;
    text-align: center;
    margin: 3%;
    width: 30%;
    height: 60%;
    background-color: #0d1019;
    border-radius: 1vh;
    cursor: pointer;
    padding: 1vh;
}

.tool-name {
    display: inline-block;
    width: 100%;
    height: 33%;
    font-size: 2vh;
    font-weight: 200;
    color: #0984e3;
}



.tool-icon-div {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: 50%;
    height: 50%;
}

.tool-link-div:hover .tool-icon {
    color: #0984e3;
    transform: scale(1.2);
    transition: ease-in-out .25s;
}

.tool-icon {
    color: white;
}

.tool-text {
    display: none;
    position: absolute;
    font-weight: 100;
    top: 0vh;
    color: white;
    font-size: 1.5vh;
    padding: 1vh;
}

.tool-link-div:hover .tool-name {
    display: none;
}

.tool-link-div:hover .tool-text {
    display: block;

}

.tool-link-div:hover {
    transform: scale(1.1);
    transition: ease-in-out;
}

.tool-link-div:hover .tool-icon {
    display: none;
}