@import url('https://fonts.googleapis.com/css?family=Audiowide');

* {
    margin: 0;
    padding: 0;
}

body {
    width: 100%;
    height: 100%;
    overflow-x: hidden;
    overflow-y: scroll;
    background-color: #0d1019;
    font-family: "Audiowide";
}

.data-hub-page {
    position: relative;
    width: 100%;
    height: 100%;
}

.hub-container {
    display: inline-block;
    position: relative;
    width: 80%;
}

.hub-body {
    display: inline-flex;
    justify-content: space-evenly;
    align-items: stretch;
}

.hub-title {
    width: 100%;
    height: 5vh;
}

.hub-menu {
    display: inline-flex;
    justify-content: space-evenly;
    width: 100%;
    height: auto;
}

.hub-left, .hub-right {
    display: inline-block;
    justify-content: center;
    width: 33vw;
    height: auto;
}

.hub-bottom-div, .hub-top-div {
    display: inline-block;
    width: 100%;
}

.hub-bottom, .hub-top{
    text-align: center;
    margin: auto;
    width: 33vw;
}

.hub-top h2 {
    color: white;
    font-size: 4vh;
    padding: 4vh;
    margin-bottom: -5vh;
}
