.side-nav {
    width: 20vw;
    background-color: #0a0d14;
    border-right: .25vh solid #353b48;
    height: 90vh;
}

.searchDiv {
    height: 25%;
    overflow-y: auto;
    border-bottom: .25vh solid #353b48;
}

.links-div {
    display: inline-flex;
    justify-content: center;
    color: #868991;
    width: 100%;
    height: 75%;
    overflow-x: hidden;
    overflow-y: auto;
}

.links-div ul {
    padding-right: 10%;
    padding-left: 10%;
    width: 100%;
    list-style-type: none;
    margin-left: 1vh;
}

.links-div li {
    padding: .5vh;
    margin: 1vh;
    border: .1vh solid white;
}

.links-div a {
    text-decoration: none;
    color: #868991;
    width: 100%;
}

.links-div a:hover {
    color: #0984e3;
    cursor: pointer;
}

